import type { LoaderArgs } from '@remix-run/cloudflare';
import { json } from '@remix-run/cloudflare';
import { Link, useLoaderData } from '@remix-run/react';
import { Materials } from 'shared/client/Materials';
import routes from 'shared/routing/routes';

import Button from '~/components/common/Button';
import { Text } from '~/components/common/Text';
import { TextVariantEnum } from '~/components/common/Text/enums';
import { NewsItemComponent } from '~/components/pages/Materials/News/NewsItem';
import { useApp } from '~/hooks';

import { BoxPagination } from './news';

export const loader = async ({ params, context }: LoaderArgs) => {
  const clientNews = new Materials();
  const newsItemId = params.id;

  const [news] = await Promise.all([clientNews.newsDetail(newsItemId || '')]);

  return json({ news });
};

export default function Index() {
  const data = useLoaderData<typeof loader>();
  const { t } = useApp();

  const newsItem = data.news.data;

  return (
    <>
      <NewsItemComponent newsItem={newsItem} />
      <BoxPagination>
        <Link to={routes.materials.news.root}>
          <Button priority='secondary'>
            <Text variant={TextVariantEnum.textBody2}>{t('home:see_all_news')}</Text>
          </Button>
        </Link>
      </BoxPagination>
    </>
  );
}
